import {Component, Input, OnInit} from '@angular/core';
import {ModalController} from "@ionic/angular";
import {ModalMapPositionComponent} from "../modal-map-position/modal-map-position.component";

@Component({
  selector: 'app-modal-request-position',
  templateUrl: './modal-request-position.component.html',
  styleUrls: ['./modal-request-position.component.scss'],
})
export class ModalRequestPositionComponent implements OnInit {

  currentModal = null;
  firstName;
  lastName;
  constructor(private modalController: ModalController) { }

  ngOnInit() {

  }

  async createModal() {
    const modal = await this.modalController.create({
      component: ModalMapPositionComponent,
    });

    await modal.present();
    this.currentModal = modal;
  }

  dismissModal() {
    if (this.currentModal) {
      this.currentModal.dismiss().then(() => { this.currentModal = null; });
    }
  }

  async close() {
    const closeModal: string = 'Modal Closed';
    await this.modalController.dismiss(closeModal);
  }
}
