import {Component, Input, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import {createAnimation} from "@ionic/core";
import {ModalController} from "@ionic/angular";
import {ModalRequestPositionComponent} from "../modal-request-position/modal-request-position.component";
import {MeetingPointModalComponent} from "../meeting-point-modal/meeting-point-modal.component";
import {ContactUser} from "../../../../model/user";

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss'],
})
export class ContactComponent implements OnInit {
  currentModal = null;
  user: ContactUser;
  toogle = false;
  textPosition = 'Partage de ma position';
  textRequestPosition = 'Demande de sa position';
  textMeetingPoint = 'Point de rencontre';
  positionType;
  closeIcon = false;
  shareMyPosition = false;
  requestPosition = false;
  meetingPoint = false;
  meeting;
  firstName = '';
  lastName = '';
  constructor(private route: ActivatedRoute, private router: Router, private modalController: ModalController) {
  }
  ngOnInit() {
    this.user = {
      id: this.router.getCurrentNavigation().extras.state.id,
      email: this.router.getCurrentNavigation().extras.state.email,
      society: this.router.getCurrentNavigation().extras.state.society,
      firstName: this.router.getCurrentNavigation().extras.state.firstName,
      lastName: this.router.getCurrentNavigation().extras.state.lastName,
      role: this.router.getCurrentNavigation().extras.state.role,
    };
    this.firstName = this.router.getCurrentNavigation().extras.state.firstName;
    this.lastName = this.router.getCurrentNavigation().extras.state.lastName;
  }

  async createModal() {
    const modal = await this.modalController.create({
      component: ModalRequestPositionComponent,
      componentProps: {
        firstName: this.firstName,
        lastName: this.lastName
      }
    });
    await modal.present();
    this.currentModal = modal;
  }
  
  dismissModal() {
    if (this.currentModal) {
      this.currentModal.dismiss().then(() => { this.currentModal = null; });
    }
  }

  async createModalMeeting(text) {
    const modal = await this.modalController.create({
      component: MeetingPointModalComponent,
      componentProps: {
        textMeeting: text,
        firstName: this.firstName,
        lastName: this.lastName
      }
    });
    await modal.present();
    this.currentModal = modal;
  }

  openBottomSheet(){
    const animation = createAnimation()
      .addElement(document.querySelector(".bottomSheet"))
      .easing("ease-in-out")
      .duration(400)
      .direction("alternate")
      .keyframes([
        { height: "0px", transform: "scale(1)", opacity: "1",},
        { height: "0px", transform: "scale(1)", opacity: "1" },
      ]);
    (<HTMLStyleElement>document.querySelector(".bottomSheet")).style.display ="block";
    animation.play();
  }

  closeBottomSheet(){
    const animation = createAnimation()
      .addElement(document.querySelector(".bottomSheet"))
      .easing("ease-in-out")
      .duration(200)
      .direction("alternate")
      .keyframes([
        { height: "0px",},
        { height:  "0px", },
      ]);
    animation.play();
  }


  toogleMode(type){
    if(type == 1) {
      this.shareMyPosition = true;
      this.requestPosition = false;
      this.meetingPoint = false;
      this.toogleShow();
    } else if(type == 2) {
      this.shareMyPosition = false;
      this.requestPosition = true;
      this.meetingPoint = false;
      this.toogleShow();
      if(this.textRequestPosition == 'Demande de sa position accord jusqu\'à la désactivation') {
        this.createModal();
        this.toogle = false;
      }
    } else if(3) {
      this.shareMyPosition = false;
      this.requestPosition = false;
      this.meetingPoint = true;
      this.toogleShow();
      if(this.meeting == 'time' || this.meeting == 'immediately') {
        this.createModalMeeting(this.textMeetingPoint);
        this.toogle = false;
      }
    }
  }

  meetingPointFunc(type) {
    if(type == 'immediately') {
      this.textMeetingPoint = 'Demande de ma rencontre immédiatement';
    } else if(type == 'time') {
      this.textMeetingPoint = 'Demande de ma rencontre À 15:29 18/08/21';
    }
    this.toogle = false;
  }

  toogleShow() {
    if(!this.toogle){
      this.toogle = true;
      this.openBottomSheet();
    }else{
      this.toogle = false;
      this.closeBottomSheet();
    }
  }

  sharePosition(type) {
    if(this.positionType) {
      this.closeIcon = true;
      if(type == 'time') {
        this.textPosition = 'Partage de ma position : Accord Jusqu\'à 15:29 18/08/21';
      } else if(type == 'disable') {
        this.textPosition = 'Partage de ma position : Accord jusqu\'à la désactivation';
      }
      this.toogle = false;
    } else {
      alert('Veuillez selectionnez un choix');
    }
  }

  resetPosition() {
    this.toogleShow();
    this.textPosition = 'Partage de ma position';
    this.closeIcon = false;
  }
  requestPositionFunc() {
    this.textRequestPosition = 'Demande de sa position : Accord jusqu\'à la désactivation';
    this.toogle = false;
  }
}
